body {
  background-color: $white;
  font-family: $gothic;
  font-size: 16px;
  min-height: 100vh;
  position: relative;
  @include media-breakpoint-up(lg){
    zoom: 90%;
  }
}
/* For Firefox */
input[type='number'] {
  -moz-appearance:textfield;
}
/* Webkit browsers like Safari and Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.section-title{
  position: relative;
  font-family: $playfair;
  font-size: 34px;
  font-weight: bold;
  color: $white;
  text-align: center;
  margin-bottom: 90px;
  &:after{
    position: absolute;
    bottom: -28px;
    left: calc(50% - 32px);
    content:'';
    width: 65px;
    height: 2px;
    background-color: #b08767;
    border: solid 3px #b08767;
    border-radius: 10px;
  }
}
.page-title{
  font-family: $playfair;
  font-size: 34px;
  font-weight: 900;
  color: #4a4a4a;
  margin-bottom: 20px;
  @include media-breakpoint-up(md){
    margin-bottom: 65px;
    font-size: 45px;
  }
  @include media-breakpoint-up(lg){
    font-size: 54px;
  }
}
.service-title{
  font-size: 18px;
  color: #4a4a4a;
  position: relative;
  margin-top: 28px;
  margin-bottom: 22px;
  &:before{
    position: absolute;
    bottom: -9px;
    left: 0;
    content: '';
    width: 65px;
    height: 4px;
    background-color: #b08767;
    border-radius: 5px;
  }
}
.input-group-count-label{
  font-family: $montserrat;
  font-size: 18px;
  color: #4a4a4a;
  margin-bottom: 4px;
  padding-left: 17px;
}
.input-group-count{
  margin-left: 17px;
  width: 114px;
  .btn{
    border-radius: 0;
    line-height: 1.33;
    &:hover, &:focus, &:active{
      box-shadow: none;
      outline: none;
    }
  }
  .form-control{
    background-color: #ffffff;
    border: solid 1px $primary;
    font-family: $montserrat;
    font-size: 18px;
    color: #4a4a4a;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    line-height: 1.33;
    &:hover, &:focus, &:active{
      box-shadow: none;
      outline: none;
    }
  }
}
.btn-add-to-cart{
  line-height: 1.9;
  width: 100%;
  @include media-breakpoint-up(sm){
    padding-left: 60px;
    padding-right: 60px;
    width: auto;
  }
  @include media-breakpoint-up(md){
    padding-left: 100px;
    padding-right: 100px;
  }
}
.service-accordion{
  .card{
    border-radius: 2px;
    background-color: #ffffff;
    border: solid 1px $primary;
    margin-bottom: 16px;
    .card-header{
      background-color: transparent;
      border: 0;
      padding: 0;
      button{
        font-family: $gothic;
        font-size: 16px;
        line-height: 1.25;
        color: #4a4a4a;
        padding: 12px 21px;
        border: 0;
        &:hover, &:active, &:focus{
          text-decoration: none;
        }
        &.btn-accordion-toggle{
          width: 80%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: left;
        }
      }
    }
    &:first-of-type{
      border: solid 1px $primary;
      border-radius: 2px;
    }
    &:not(:first-of-type):not(:last-of-type){
      border: solid 1px $primary;
      border-radius: 2px;
    }
    &:last-of-type {
      border: solid 1px $primary;
      border-radius: 2px;
    }
  }
}
.btn-accordion-toggle{
  &+.btn{
    border-radius: 0;
    &:hover, &:focus, &:active{
      box-shadow: none;
      outline: none;
    }
  }
  &[aria-expanded="false"] {
    &+.btn{
      &:before{
        content: '+';
        color: #fff;
        font-size: 14px;
      }
    }
  }

  &[aria-expanded="true"] {
    &+.btn{
      &:before{
        content: '-';
        color: #fff;
        font-size: 14px;
      }
    }
  }

}
.select-label{
  font-size: 16px;
  line-height: 1.25;
  color: #4a4a4a;
}
.check-icon{
  font-size: 70px !important;
  color: #7ed321;
}
.modal-p{
  font-size: 18px;
  color: #4a4a4a;
}
.modal-custom{
  .modal-dialog{
    @include media-breakpoint-up(xs){
      max-width: auto;
    }
    @include media-breakpoint-up(sm){
      max-width: 540px;
    }
    @include media-breakpoint-up(md){
      max-width: 720px;
    }
    @include media-breakpoint-up(lg){
      max-width: 960px;
    }
    @include media-breakpoint-up(xl){
      max-width: 1140px;
    }
  }
}
.text-ask{
  color: #3a3a3a;
}
.services-page{
  background-color: $white;
  padding: 175px 0;
  @include media-breakpoint-up(md){
    padding-bottom: 460px;
  }
  .section-title{
    color: #4a4a4a !important;
  }
  .slick-prev:before, .slick-next:before{
    color: #b08767 !important;
  }
  .slick-dots li button:before {
    border: 1px solid #b08767 !important;
    background-color: #b08767 !important;
  }
  .slick-dots li.slick-active button:before {
    background-color: transparent !important;
  }
}
.btn-rounded{
  border-radius: 50px !important;
}
.mt-15{
  margin-top: 15px !important;
}
.mb-15{
  margin-bottom: 15px !important;
}
.mb-20{
  margin-bottom: 20px !important;
}
.mb-25{
  margin-bottom: 25px !important;
}
.mb-30{
  margin-bottom: 30px !important;
}
.mb-35{
  margin-bottom: 35px !important;
}
.mb-45{
  margin-bottom: 45px;
}
.btn-outline-primary{
  &:hover, &:focus, &:active{
    color: #fff;
  }
}
.custom-select-reset{
  border-radius: 2px;
  border: solid 1px $primary;
  height: 44px;
  background: #fff url(data:image/svg+xml;base64,) no-repeat right -1px center;
  background-size: contain;
  &:focus{
    border: solid 1px $primary;
  }
}
.modal-custom{
  .modal-content{
    border-radius: 2px;
    background-color: #ffffff;
    border: solid 10px $primary;
  }
}
.modal-backdrop.show {
  opacity: 0.3;
}
.modal-close-btn{
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 32px;
  color: $primary;
  opacity: 1;
  z-index: 99;
  &:hover, &:focus, &:active{
    color: darken($primary, 10%);
    outline: none;
    box-shadow: none;
  }
  span{
    &:hover, &:focus, &:active{
      color: darken($primary, 10%);
      outline: none;
      box-shadow: none;
    }
  }
}
.form-control-reset{
  line-height: 1.9;
  height: 44px;
}
.btn-reset{
  line-height: 1.9;
}
.form-control-primary{
  border-color: $primary;
  border-radius: 2px;
  &:focus{
    border-color: $primary;
  }
}
textarea.form-control-primary{
  height: auto;
}
.custom-file-reset{
  &:focus{
    outline: none;
    box-shadow: none;
  }
  .custom-file-label{
    height: 44px;
    line-height: 1.9;
    background-color: #f5f5f5;
    border: 1px solid $primary;
    border-radius: 2px;
    &:hover{
      cursor: pointer;
    }
    &:after {
      font-family: $fontawesome;
      color: $white;
      content: "\f0c6";
      height: 42px;
      font-size: 24px;
      line-height: 32px;
      background-color: $primary;
      border-left: 1px solid $primary;
      border-radius: 0 2px 2px 0;
    }
  }
  .custom-file-input {
    height: 44px;
    &:focus{
      outline: none;
      box-shadow: none;
    }
  }
  .custom-file-input:lang(en) ~ .custom-file-label::after{
    content: "\f0c6";
  }
  .custom-file-input:focus ~ .custom-file-label {
    border-color: $primary;
  }
}
.content-wrapper{
  padding-top: 160px;
  padding-bottom: 80px;
  @include media-breakpoint-up(md){
    padding-bottom: 448px;
  }
}
.policy-text{
  font-size: 16px;
  line-height: 1.44;
  color: #4a4a4a;
}
.list-group-item{
  border-color: $primary;
}
.list-group-item:first-child {
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.list-group-item:last-child {
  border-bottom-right-radius: 2px;
  border-bottom-left-radius: 2px;
}
.list-group-item-action{
  font-size: 16px;
  line-height: 1.25;
  color: #4a4a4a;
}
.list-group-item.router-link-exact-active {
  z-index: 2;
  color: #fff;
  background-color: #ed7a82;
  border-color: #ed7a82;
}
.btn-link:hover, .btn-link:focus{
  text-decoration: none;
}
.btn-transparent{
  border: 0;
  outline: none;
  background-color: transparent;
  &:hover, &:focus, &:active{
    outline: none;
    box-shadow: none;
  }
}
.border-radius-0{
  border-radius: 0;
}
.input-group.input-group-custom{
  .form-control{
    border-color: $primary;
  }
  .fa-calendar{
    font-size: 24px;
  }
}
.input-group.input-group-custom > .form-control:not(:last-child), .input-group.input-group-custom > .custom-select:not(:last-child) {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.input-group.input-group-custom > .input-group-append > .btn, .input-group.input-group-custom > .input-group-append > .input-group-text, .input-group.input-group-custom > .input-group-prepend:not(:first-child) > .btn, .input-group.input-group-custom > .input-group-prepend:not(:first-child) > .input-group-text, .input-group.input-group-custom > .input-group-prepend:first-child > .btn:not(:first-child), .input-group.input-group-custom > .input-group-prepend:first-child > .input-group-text:not(:first-child){
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.custom-label{
  font-size: 18px;
}
.bootstrap-datetimepicker-widget table th,.bootstrap-datetimepicker-widget table td.day{
  height: 30px;
}
.bootstrap-datetimepicker-widget table td.active, .bootstrap-datetimepicker-widget table td.active:hover{
  background-color: $primary;
}
.card-form-page{
  border: 1px solid $primary;
  border-radius: 2px;
  height: 100%;
  .card-body {
    padding: 14px 12px;
  }
}
.custom-checkbox-reset{
  .custom-control-label{
    line-height: 32px;
    color: #4a4a4a;
    &::before {
      width: 20px;
      height: 20px;
      border-radius: 2px;
      border: solid 1px #ed7a82;
      background-color: $white;
    }
    &::after {
      width: 20px;
      height: 20px;
    }
  }
}

.link-blue{
  color: #4a90e2 !important;
  &:hover, &:focus, &:active{
    color: #4a90e2 !important;
    text-decoration: underline;
  }
}
.hr-form{
  border-top: solid 3px rgba(151, 151, 151, 0.5);
}
.hr-dotted{
  border-top: dotted 2px rgba(151, 151, 151, 0.5);
}
.form-profile-avatar{
  position: relative;
  width: 262px;
  height: 270px;
  //padding: 14px;
  background-color: #edf0f4;
  border: solid 4px #edf0f4;
  i{
    font-size: 108px;
    color: rgba(175, 193, 206, 0.57);
    margin-bottom: calc(14px + 38px + 44px);
    margin-top: 60px;
  }
  button{
    position: absolute;
    bottom: 14px;
    left: 14px;
    right: 14px;
    width: calc(100% - 28px);
  }
}
.profile-bg-container{
  height: 357px;
  background-image: url("../images/detail-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.content-wrapper-profile{
  margin-top: -130px;
}
.fc{
  min-width: 450px;
}
.fc-license-message{
  display: none;
}
.fc-scroller{
  height: auto !important;
}
.fc-view-container{
  background-color: #ffffff;
}
.fc-event {
  border-radius: 0 !important;
  border: 0 !important;
}
.fc-header-toolbar{
  display: none;
}
.form-client-bg-container{
  height: 200px;
  background-image: url("../images/client-form-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  @include media-breakpoint-up(md){
    height: 355px;
  }
}
.content-wrapper-client-form{
  padding-top: 4rem;
  @include media-breakpoint-up(md){
    padding-bottom: 550px;
  }
}


